import React from 'react';

type Props = {
  children?: JSX.Element;
};

const Scroll = (props: Props): JSX.Element => {
  const children = { props };

  return (
    <div
      style={{
        overflowY: 'scroll',
        border: '1px solid black',
        height: '500px',
      }}
    >
      {children}
    </div>
  );
};

export default Scroll;
